@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
:root{
  --accent-color: #8672ff;
  --base-color: white;
  --text-color: #2E2B41;
  --input-color: #f3f0ff;
}

*{
  margin: 0;
  padding: 0;
}

html{
  font-family: Poppins,Segoe UI,sans-serif;
  font-size: 12pt;
  color: var(--text-color);
  text-align: center;
}

body{
  min-height: 100vh;
  background-image: url(background.jpg);
  background-size: cover;
  background-position: right;
  overflow: hidden;
}

.wrapper{
  box-sizing: border-box;
  background-color: var(--base-color);
  height: 100vh;
  width: max(40%, 600px);
  padding: 10px;
  border-radius: 0 20px 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h1{
  font-size: 3rem;
  font-weight: 900;
  text-transform: uppercase;
}

form{
  width: min(400px,100%);
  margin-top: 20px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
form > div{
  width: 100%;
  display: flex;
  justify-content: center;
}

form label{
  flex-shrink: 0;
  height: 50px;
  width: 50px;
  background-color: var(--accent-color);
  fill: black;
  color: black;
  border-radius: 10px 0 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 500;
}

form input{
  box-sizing: border-box;
  flex-grow: 1;
  min-width: 0;
  height: 50px;
  padding: 1em;
  font: inherit;
  border-radius: 0 10px 10px 0;
  border-left: none;
  border: none;
  background-color: var(--input-color);
  transition: 150ms ease;
}

form input:hover{
  border-color: var(--accent-color)
}

form input:focus{
  outline:none;
  border-color: var(--text-color);
}

div:has(input:focus) > label{
  background-color:var(--text-color);
}

form button{
  margin-top: 10px;
  border: none;
  border-radius: 1000px;
  padding: .85em 4em;
  background-color: var(--accent-color);
  font: inherit;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
  transition: 150ms ease;
}

form button:hover{
  background-color: var(--text-color);
}

form button:focus{
  outline: none;
  background-color: var(--text-color);
}

a{
  text-decoration: none;
}

a:hover{
  text-decoration: underline;
}
@media(max-width: 1100px){
  .wrapper{
    width:min(600px,100%);
    border-radius: 0;
    
  }
}


